'use client'

import { useState, useEffect, useMemo, useCallback } from 'react';
import { AdminRouteGuard } from '@/components/admin-route-guard';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Badge } from '@/components/ui/badge';
import { Checkbox } from '@/components/ui/checkbox';
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select';
import {
  Tabs,
  TabsContent,
  TabsList,
  TabsTrigger,
} from '@/components/ui/tabs';
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from '@/components/ui/table';
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from '@/components/ui/dropdown-menu';
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogFooter,
  DialogHeader,
  DialogTitle,
} from '@/components/ui/dialog';
import { toast } from 'sonner';
import {
  Users,
  Shield,
  Edit,
  UserCheck,
  Search,
  Filter,
  RefreshCw,
  MoreHorizontal,
  ChevronLeft,
  ChevronRight,
  Loader2,
  AlertCircle,
  UserX,
  Activity,
  Clock,
  Upload,
  HardDrive,
  TrendingUp,
  Calendar,
  BarChart3
} from 'lucide-react';
import { AdminUser, UserFilters, UserStats, UserStatus } from '@/types/admin';
import { UserRole } from '@/types/review';
import { formatDistanceToNow } from 'date-fns';
import { useDebounce } from '@/lib/hooks/use-debounce';

export default function AdminUsersPage() {
  // State management
  const [users, setUsers] = useState<AdminUser[]>([]);
  const [stats, setStats] = useState<UserStats | null>(null);
  const [loading, setLoading] = useState(true);
  const [refreshing, setRefreshing] = useState(false);
  const [selectedUsers, setSelectedUsers] = useState<Set<string>>(new Set());

  // Upload stats state
  const [uploadStats, setUploadStats] = useState<any[]>([]);
  const [overallStats, setOverallStats] = useState<any | null>(null);
  const [loadingUploadStats, setLoadingUploadStats] = useState(false);
  const [timeRangeFilter, setTimeRangeFilter] = useState<'7d' | '30d' | '90d' | 'all'>('all');

  // Filters
  const [searchQuery, setSearchQuery] = useState('');
  const [roleFilter, setRoleFilter] = useState<UserRole | ''>('all');
  const [statusFilter, setStatusFilter] = useState<'active' | 'inactive' | ''>('all');
  const [currentPage, setCurrentPage] = useState(1);
  const [totalPages, setTotalPages] = useState(1);
  const [pageSize] = useState(20);

  // Sorting
  const [sortField, setSortField] = useState<'name' | 'activity' | 'role' | 'status' | 'created_at' | ''>('');
  const [sortDirection, setSortDirection] = useState<'asc' | 'desc'>('asc');

  // Dialogs
  const [roleChangeDialog, setRoleChangeDialog] = useState<{ open: boolean; user: AdminUser | null }>({ open: false, user: null });
  const [editorAssignDialog, setEditorAssignDialog] = useState<{ open: boolean; user: AdminUser | null }>({ open: false, user: null });
  const [bulkActionDialog, setBulkActionDialog] = useState<{ open: boolean; action: string }>({ open: false, action: '' });
  const [confirmDialog, setConfirmDialog] = useState<{ open: boolean; action: () => void; title: string; description: string }>({
    open: false,
    action: () => {},
    title: '',
    description: ''
  });

  // Dialog form state
  const [newRole, setNewRole] = useState<UserRole>('transcriptionist');
  const [selectedEditorId, setSelectedEditorId] = useState<string>('');
  const [editors, setEditors] = useState<{ id: string; name: string; email: string }[]>([]);

  // Debounced search
  const debouncedSearch = useDebounce(searchQuery, 300);

  // Sorting handler
  const handleSort = useCallback((field: 'name' | 'activity' | 'role' | 'status' | 'created_at') => {
    if (sortField === field) {
      // Toggle direction if same field
      setSortDirection(sortDirection === 'asc' ? 'desc' : 'asc');
    } else {
      // New field, default to ascending for most fields, descending for activity
      setSortField(field);
      setSortDirection(field === 'activity' ? 'desc' : 'asc');
    }
    setCurrentPage(1); // Reset to first page when sorting
  }, [sortField, sortDirection]);

  // Fetch users data
  const fetchUsers = useCallback(async () => {
    try {
      // Ensure currentPage is at least 1
      const safePage = Math.max(1, currentPage);

      const filters: UserFilters = {
        search: debouncedSearch || undefined,
        role: roleFilter === 'all' ? undefined : roleFilter || undefined,
        isActive: statusFilter === 'all' ? undefined : statusFilter === 'active' ? true : statusFilter === 'inactive' ? false : undefined,
        page: safePage,
        pageSize,
        sortField: sortField || undefined,
        sortDirection: sortDirection || undefined
      };


      const queryParams = new URLSearchParams();
      if (filters.search) queryParams.append('search', filters.search);
      if (filters.role) queryParams.append('role', filters.role);
      if (filters.isActive !== undefined) queryParams.append('isActive', String(filters.isActive));
      queryParams.append('page', String(filters.page));
      queryParams.append('pageSize', String(filters.pageSize));
      queryParams.append('includeStats', currentPage === 1 ? 'true' : 'false');
      if (filters.sortField) queryParams.append('sortField', filters.sortField);
      if (filters.sortDirection) queryParams.append('sortDirection', filters.sortDirection);

      console.log('🔄 AdminUsersPage: fetchUsers called');
      console.log('🔄 AdminUsersPage: Sort params - field:', filters.sortField, 'direction:', filters.sortDirection);

      const { authManager } = await import('@/lib/auth-utils');

      // Add a small delay to ensure AuthManager is fully ready
      await new Promise(resolve => setTimeout(resolve, 100));

      // First check if user is logged in
      const isLoggedIn = await authManager.isLoggedIn();
      console.log('🔄 AdminUsersPage: User login status:', isLoggedIn);

      if (!isLoggedIn) {
        console.log('❌ AdminUsersPage: User not logged in, redirecting to login');
        window.location.href = '/login';
        return;
      }

      console.log('✅ AdminUsersPage: User is logged in, proceeding with authentication');

      let response;

      // If we have a cached token, skip cookie attempt and go straight to Authorization header
      if (authManager.hasValidToken()) {
        console.log('Using cached token for immediate auth');
        const token = await authManager.getAuthToken();
        if (token) {
          response = await fetch(`/api/admin/users?${queryParams}`, {
            headers: {
              'Authorization': `Bearer ${token}`
            }
          });
        } else {
          console.log('Cached token was invalid, forcing session refresh');
          // Force refresh and try again
          const refreshedToken = await authManager.forceRefreshSession();
          if (refreshedToken) {
            response = await fetch(`/api/admin/users?${queryParams}`, {
              headers: {
                'Authorization': `Bearer ${refreshedToken}`
              }
            });
          } else {
            // Fall back to cookie auth
            response = await fetch(`/api/admin/users?${queryParams}`, {
              credentials: 'include'
            });
          }
        }
      } else {
        console.log('No valid cached token, trying cookie auth first');
        // Try with credentials first, fallback to manual token
        response = await fetch(`/api/admin/users?${queryParams}`, {
          credentials: 'include'
        });

        // If that fails with 401, try with Authorization header
        if (!response.ok && response.status === 401) {
          console.log('Cookie auth failed, trying Authorization header...');
          const token = await authManager.getAuthToken();

          console.log('Auth manager token result:', { hasToken: !!token, tokenLength: token?.length });

          if (token) {
            console.log('Using Authorization header with token, token length:', token.length);
            response = await fetch(`/api/admin/users?${queryParams}`, {
              headers: {
                'Authorization': `Bearer ${token}`
              }
            });
            console.log('Authorization header response status:', response.status);

            // Log response details for debugging
            if (!response.ok) {
              const errorText = await response.text();
              console.log('Response error details:', errorText);
            }
          } else {
            console.log('No session token available, trying force refresh...');
            // Final fallback: force refresh the session
            const refreshedToken = await authManager.forceRefreshSession();
            if (refreshedToken) {
              console.log('Force refresh successful, retrying with refreshed token');
              response = await fetch(`/api/admin/users?${queryParams}`, {
                headers: {
                  'Authorization': `Bearer ${refreshedToken}`
                }
              });
            } else {
              console.log('Force refresh failed, all authentication methods exhausted');
            }
          }
        }
      }

      const data = await response.json();

      if (!response.ok) {
        throw new Error(data.error || 'Failed to fetch users');
      }

      // Debug: Log received users and their transcription counts
      console.log('🔄 AdminUsersPage: Received users from backend:', data.users?.length || 0);
      data.users?.forEach((user, index) => {
        console.log(`🔄 AdminUsersPage: User ${index + 1}: ${user.id} - ${user.transcription_count} transcriptions`);
      });

      setUsers(data.users);
      if (data.stats) {
        setStats(data.stats);
      }

      // Ensure totalPages is at least 1
      const calculatedTotalPages = Math.ceil((data.total || 0) / pageSize);
      setTotalPages(Math.max(1, calculatedTotalPages));
      } catch (error: any) {
        console.error('Error fetching users:', error);

        // Debug authentication when "No auth token" error occurs
        if (error.message?.includes('No auth token')) {
          console.log('🚨 AUTH DEBUG: "No auth token" error detected, running debug...');
          const { debugAuth } = await import('@/lib/auth-utils');
          const debugResult = await debugAuth();
          console.log('🚨 AUTH DEBUG: Debug result:', debugResult);
        }

        // Clear auth cache on authentication errors
        if (error.message?.includes('401') || error.message?.includes('Unauthorized') || error.message?.includes('No auth token')) {
          const { authManager } = await import('@/lib/auth-utils');
          authManager.handleAuthError().catch(console.error);
        }
        toast.error(error.message || 'Failed to load users');
      } finally {
        setLoading(false);
        setRefreshing(false);
      }
  }, [debouncedSearch, roleFilter, statusFilter, currentPage, pageSize, sortField, sortDirection]);

  // Fetch editors for assignment
  const fetchEditors = useCallback(async () => {
    try {
      const { authManager } = await import('@/lib/auth-utils');

      // First check if user is logged in
      const isLoggedIn = await authManager.isLoggedIn();
      if (!isLoggedIn) {
        console.log('User not logged in, skipping editors fetch');
        return;
      }

      let response;

      // If we have a cached token, skip cookie attempt and go straight to Authorization header
      if (authManager.hasValidToken()) {
        console.log('Using cached token for editors');
        const token = await authManager.getAuthToken();
        response = await fetch('/api/admin/users?role=editor&pageSize=100', {
          headers: {
            'Authorization': `Bearer ${token}`
          }
        });
      } else {
        response = await fetch('/api/admin/users?role=editor&pageSize=100', {
          credentials: 'include'
        });

        // If that fails with 401, try with Authorization header
        if (!response.ok && response.status === 401) {
          console.log('Cookie auth failed for editors, trying Authorization header...');
          const token = await authManager.getAuthToken();

          if (token) {
            response = await fetch('/api/admin/users?role=editor&pageSize=100', {
              headers: {
                'Authorization': `Bearer ${token}`
              }
            });
          }
        }
      }

      const data = await response.json();

      if (response.ok && data.users) {
        setEditors(data.users.map((u: AdminUser) => ({
          id: u.id,
          name: u.full_name || u.email,
          email: u.email
        })));
      }
    } catch (error) {
      console.error('Error fetching editors:', error);

      // Debug authentication when "No auth token" error occurs
      if (String(error).includes('No auth token')) {
        console.log('🚨 AUTH DEBUG: "No auth token" error detected in fetchEditors, running debug...');
        const { debugAuth } = await import('@/lib/auth-utils');
        const debugResult = await debugAuth();
        console.log('🚨 AUTH DEBUG: Debug result:', debugResult);
      }

      // Clear auth cache on authentication errors
      if (String(error).includes('401') || String(error).includes('Unauthorized') || String(error).includes('No auth token')) {
        const { authManager } = await import('@/lib/auth-utils');
        authManager.handleAuthError();
      }
    }
  }, []);

  // Fetch upload statistics
  const fetchUploadStats = useCallback(async () => {
    try {
      setLoadingUploadStats(true);

      const { authManager } = await import('@/lib/auth-utils');

      // First check if user is logged in
      const isLoggedIn = await authManager.isLoggedIn();
      if (!isLoggedIn) {
        console.log('User not logged in, skipping upload stats fetch');
        setLoadingUploadStats(false);
        return;
      }

      let response;

      // If we have a cached token, skip cookie attempt and go straight to Authorization header
      if (authManager.hasValidToken()) {
        console.log('Using cached token for upload stats');
        const token = await authManager.getAuthToken();
        response = await fetch(`/api/admin/users?type=upload-stats&timeRange=${timeRangeFilter}`, {
          headers: {
            'Authorization': `Bearer ${token}`
          }
        });
      } else {
        // Try with credentials first, fallback to manual token
        response = await fetch(`/api/admin/users?type=upload-stats&timeRange=${timeRangeFilter}`, {
          credentials: 'include'
        });

        // If that fails with 401, try with Authorization header
        if (!response.ok && response.status === 401) {
          console.log('Cookie auth failed for upload stats, trying Authorization header...');
          const token = await authManager.getAuthToken();

          console.log('Upload stats auth manager token result:', { hasToken: !!token, tokenLength: token?.length });

          if (token) {
            console.log('Using Authorization header with token for upload stats, token length:', token.length);
            response = await fetch(`/api/admin/users?type=upload-stats&timeRange=${timeRangeFilter}`, {
              headers: {
                'Authorization': `Bearer ${token}`
              }
            });
            console.log('Upload stats Authorization header response status:', response.status);

            // Log response details for debugging
            if (!response.ok) {
              const errorText = await response.text();
              console.log('Upload stats response error details:', errorText);
            }
          } else {
            console.log('No session token available for upload stats');
          }
        }
      }

      const data = await response.json();

      if (!response.ok) {
        throw new Error(data.error || 'Failed to fetch upload stats');
      }

      setUploadStats(data.userStats);
      setOverallStats(data.overallStats);
    } catch (error: any) {
      console.error('Error fetching upload stats:', error);

      // Debug authentication when "No auth token" error occurs
      if (error.message?.includes('No auth token')) {
        console.log('🚨 AUTH DEBUG: "No auth token" error detected in fetchUploadStats, running debug...');
        const { debugAuth } = await import('@/lib/auth-utils');
        const debugResult = await debugAuth();
        console.log('🚨 AUTH DEBUG: Debug result:', debugResult);
      }

      // Clear auth cache on authentication errors
      if (error.message?.includes('401') || error.message?.includes('Unauthorized') || error.message?.includes('No auth token')) {
        const { authManager } = await import('@/lib/auth-utils');
        authManager.handleAuthError();
      }
      toast.error(error.message || 'Failed to load upload statistics');
    } finally {
      setLoadingUploadStats(false);
    }
  }, [timeRangeFilter]);

  useEffect(() => {
    // Wait for AuthManager initialization before making API calls
    const initializeAndFetch = async () => {
      console.log('🔄 AdminUsersPage: Waiting for AuthManager initialization...');
      const { authManager } = await import('@/lib/auth-utils');

      // Wait a bit more to ensure AuthManager is fully ready
      await new Promise(resolve => setTimeout(resolve, 200));

      console.log('✅ AdminUsersPage: AuthManager ready, starting fetch...');
      fetchUsers();
    };

    initializeAndFetch();

    // Add debug commands to window for manual testing
    (window as any).debugAuth = async () => {
      console.log('🔧 Manual debug triggered...');
      const { debugAuth } = await import('@/lib/auth-utils');
      return await debugAuth();
    };

    (window as any).testAuthManager = async () => {
      console.log('🔧 Testing AuthManager directly...');
      const { authManager } = await import('@/lib/auth-utils');
      const token = await authManager.getAuthToken();
      console.log('🔧 AuthManager test result:', { hasToken: !!token, tokenLength: token?.length });
      return token;
    };

    (window as any).testSorting = () => {
      console.log('🔧 Testing current sorting state...');
      console.log('🔧 Current sortField:', sortField);
      console.log('🔧 Current sortDirection:', sortDirection);
      console.log('🔧 Current users count:', users.length);
      users.forEach((user, index) => {
        console.log(`🔧 User ${index + 1}: ${user.transcription_count} transcriptions`);
      });
    };

    (window as any).testAuthFixes = async () => {
      console.log('🔧 Testing authentication fixes...');
      const { testAuthFixes } = await import('@/lib/auth-utils');
      return await testAuthFixes();
    };

    // Auto-run debug on page load to identify the issue
    const runDebug = async () => {
      console.log('🚨 AUTO DEBUG: Running auth debug on page load...');
      try {
        const { debugAuth } = await import('@/lib/auth-utils');
        const result = await debugAuth();
        console.log('🚨 AUTO DEBUG: Result:', result);
      } catch (error) {
        console.error('🚨 AUTO DEBUG: Failed:', error);
      }
    };

    // Run debug after a short delay to let everything initialize
    setTimeout(runDebug, 1000);
  }, [fetchUsers]);

  useEffect(() => {
    fetchEditors();
  }, [fetchEditors]);

  useEffect(() => {
    fetchUploadStats();
  }, [fetchUploadStats]);

  // Handle user role change
  const handleRoleChange = async (userId: string, newRole: UserRole) => {
    try {
      const response = await fetch(`/api/admin/users/${userId}`, {
        method: 'PATCH',
        headers: { 'Content-Type': 'application/json' },
        credentials: 'include',
        body: JSON.stringify({ role: newRole })
      });

      if (!response.ok) {
        const error = await response.json();
        throw new Error(error.error || 'Failed to update role');
      }

      toast.success('User role updated successfully');
      fetchUsers();
      setRoleChangeDialog({ open: false, user: null });
    } catch (error: any) {
      toast.error(error.message || 'Failed to update user role');
    }
  };

  // Handle editor assignment
  const handleEditorAssignment = async (userId: string, editorId: string | null) => {
    try {
      const response = await fetch(`/api/admin/users/${userId}`, {
        method: 'PATCH',
        headers: { 'Content-Type': 'application/json' },
        credentials: 'include',
        body: JSON.stringify({ assignedEditorId: editorId })
      });

      if (!response.ok) {
        const error = await response.json();
        throw new Error(error.error || 'Failed to assign editor');
      }

      toast.success('Editor assigned successfully');
      fetchUsers();
      setEditorAssignDialog({ open: false, user: null });
    } catch (error: any) {
      toast.error(error.message || 'Failed to assign editor');
    }
  };

  // Handle user deactivation
  const handleDeactivateUser = async (userId: string) => {
    try {
      const response = await fetch(`/api/admin/users/${userId}`, {
        method: 'DELETE',
        credentials: 'include'
      });

      if (!response.ok) {
        const error = await response.json();
        throw new Error(error.error || 'Failed to deactivate user');
      }

      toast.success('User deactivated successfully');
      fetchUsers();
    } catch (error: any) {
      toast.error(error.message || 'Failed to deactivate user');
    }
  };

  // Handle bulk actions
  const handleBulkAction = async (action: string, payload: any) => {
    try {
      const response = await fetch('/api/admin/users/bulk', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        credentials: 'include',
        body: JSON.stringify({
          userIds: Array.from(selectedUsers),
          ...payload
        })
      });

      const result = await response.json();

      if (!response.ok) {
        throw new Error(result.error || 'Bulk action failed');
      }

      toast.success(`Successfully updated ${result.updated_count} users`);
      if (result.error_count > 0) {
        toast.warning(`Failed to update ${result.error_count} users`);
      }

      setSelectedUsers(new Set());
      fetchUsers();
      setBulkActionDialog({ open: false, action: '' });
    } catch (error: any) {
      toast.error(error.message || 'Bulk action failed');
    }
  };

  // Get user status
  const getUserStatus = (user: AdminUser): { status: UserStatus; label: string; variant: 'default' | 'secondary' | 'destructive' | 'outline' } => {
    if (!user.is_active) {
      return { status: 'inactive', label: 'Inactive', variant: 'destructive' };
    }
    if (!user.email_confirmed_at) {
      return { status: 'pending_confirmation', label: 'Pending', variant: 'outline' };
    }
    if (!user.last_sign_in_at) {
      return { status: 'never_logged_in', label: 'Never logged in', variant: 'secondary' };
    }
    
    const lastActive = user.last_active || user.last_sign_in_at;
    const daysSinceActive = lastActive 
      ? Math.floor((Date.now() - new Date(lastActive).getTime()) / (1000 * 60 * 60 * 24))
      : null;

    if (daysSinceActive && daysSinceActive <= 30) {
      return { status: 'active', label: 'Active', variant: 'default' };
    }

    return { status: 'inactive', label: `Inactive ${daysSinceActive} days`, variant: 'secondary' };
  };

  // Toggle user selection
  const toggleUserSelection = (userId: string) => {
    const newSelection = new Set(selectedUsers);
    if (newSelection.has(userId)) {
      newSelection.delete(userId);
    } else {
      newSelection.add(userId);
    }
    setSelectedUsers(newSelection);
  };

  // Toggle all users selection
  const toggleAllUsers = () => {
    if (selectedUsers.size === users.length) {
      setSelectedUsers(new Set());
    } else {
      setSelectedUsers(new Set(users.map(u => u.id)));
    }
  };

  return (
    <AdminRouteGuard>
      <div className="container mx-auto py-6 space-y-6">
        {/* Header */}
        <div className="flex items-center justify-between">
          <div>
            <h1 className="text-3xl font-bold tracking-tight">Admin Dashboard</h1>
            <p className="text-muted-foreground">Manage users and monitor upload activity</p>
          </div>
        </div>

        <Tabs defaultValue="users" className="space-y-6">
          <TabsList>
            <TabsTrigger value="users">User Management</TabsTrigger>
            <TabsTrigger value="uploads">Upload Statistics</TabsTrigger>
          </TabsList>

          <TabsContent value="users" className="space-y-6">
            {/* Header with stats */}
            <div className="flex flex-col gap-6">
              <div className="flex items-center justify-between">
                <div>
                  <h2 className="text-2xl font-semibold tracking-tight">Users</h2>
                  <p className="text-muted-foreground">Manage user accounts, roles, and permissions</p>
                </div>
                <Button
                  onClick={() => { setRefreshing(true); fetchUsers(); }}
                  variant="outline"
                  size="sm"
                  disabled={refreshing}
                >
                  {refreshing ? (
                    <Loader2 className="h-4 w-4 animate-spin" />
                  ) : (
                    <RefreshCw className="h-4 w-4" />
                  )}
                  Refresh
                </Button>
              </div>

          {/* Stats cards */}
          {stats && (
            <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-4">
              <Card>
                <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                  <CardTitle className="text-sm font-medium">Total Users</CardTitle>
                  <Users className="h-4 w-4 text-muted-foreground" />
                </CardHeader>
                <CardContent>
                  <div className="text-2xl font-bold">{stats.total_users}</div>
                  <p className="text-xs text-muted-foreground">
                    {stats.active_users} active, {stats.inactive_users} inactive
                  </p>
                </CardContent>
              </Card>

              <Card>
                <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                  <CardTitle className="text-sm font-medium">Admins</CardTitle>
                  <Shield className="h-4 w-4 text-muted-foreground" />
                </CardHeader>
                <CardContent>
                  <div className="text-2xl font-bold">{stats.admins_count}</div>
                  <p className="text-xs text-muted-foreground">System administrators</p>
                </CardContent>
              </Card>

              <Card>
                <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                  <CardTitle className="text-sm font-medium">Editors</CardTitle>
                  <Edit className="h-4 w-4 text-muted-foreground" />
                </CardHeader>
                <CardContent>
                  <div className="text-2xl font-bold">{stats.editors_count}</div>
                  <p className="text-xs text-muted-foreground">Content reviewers</p>
                </CardContent>
              </Card>

              <Card>
                <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                  <CardTitle className="text-sm font-medium">Transcriptionists</CardTitle>
                  <UserCheck className="h-4 w-4 text-muted-foreground" />
                </CardHeader>
                <CardContent>
                  <div className="text-2xl font-bold">{stats.transcriptionists_count}</div>
                  <p className="text-xs text-muted-foreground">Content creators</p>
                </CardContent>
              </Card>
            </div>
          )}
        </div>

        {/* Filters and actions */}
        <Card>
          <CardHeader>
            <div className="flex items-center justify-between">
              <CardTitle>Users</CardTitle>
              {selectedUsers.size > 0 && (
                <div className="flex items-center gap-2">
                  <span className="text-sm text-muted-foreground">
                    {selectedUsers.size} selected
                  </span>
                  <DropdownMenu>
                    <DropdownMenuTrigger asChild>
                      <Button variant="outline" size="sm">
                        Bulk Actions
                      </Button>
                    </DropdownMenuTrigger>
                    <DropdownMenuContent>
                      <DropdownMenuLabel>Bulk Actions</DropdownMenuLabel>
                      <DropdownMenuSeparator />
                      <DropdownMenuItem onClick={() => setBulkActionDialog({ open: true, action: 'role' })}>
                        Change Role
                      </DropdownMenuItem>
                      <DropdownMenuItem onClick={() => setBulkActionDialog({ open: true, action: 'editor' })}>
                        Assign Editor
                      </DropdownMenuItem>
                      <DropdownMenuItem onClick={() => setBulkActionDialog({ open: true, action: 'deactivate' })}>
                        Deactivate Users
                      </DropdownMenuItem>
                    </DropdownMenuContent>
                  </DropdownMenu>
                </div>
              )}
            </div>
          </CardHeader>
          <CardContent>
            {/* Filter bar */}
            <div className="flex flex-col sm:flex-row gap-4 mb-6">
              <div className="relative flex-1">
                <Search className="absolute left-2 top-2.5 h-4 w-4 text-muted-foreground" />
                <Input
                  placeholder="Search by name or email..."
                  value={searchQuery}
                  onChange={(e) => setSearchQuery(e.target.value)}
                  className="pl-8"
                />
              </div>
              <Select value={roleFilter} onValueChange={(value: any) => setRoleFilter(value === 'all' ? '' : value)}>
                <SelectTrigger className="w-[180px]">
                  <SelectValue placeholder="All roles" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">All roles</SelectItem>
                  <SelectItem value="admin">Admin</SelectItem>
                  <SelectItem value="editor">Editor</SelectItem>
                  <SelectItem value="transcriptionist">Transcriptionist</SelectItem>
                </SelectContent>
              </Select>
              <Select value={statusFilter} onValueChange={(value: any) => setStatusFilter(value === 'all' ? '' : value)}>
                <SelectTrigger className="w-[180px]">
                  <SelectValue placeholder="All statuses" />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="all">All statuses</SelectItem>
                  <SelectItem value="active">Active</SelectItem>
                  <SelectItem value="inactive">Inactive</SelectItem>
                </SelectContent>
              </Select>
            </div>

            {/* Users table */}
            {loading ? (
              <div className="flex items-center justify-center py-12">
                <Loader2 className="h-8 w-8 animate-spin text-primary" />
              </div>
            ) : users.length === 0 ? (
              <div className="flex flex-col items-center justify-center py-12 text-center">
                <Users className="h-12 w-12 text-muted-foreground mb-4" />
                <h3 className="text-lg font-semibold">No users found</h3>
                <p className="text-sm text-muted-foreground">
                  Try adjusting your search or filters
                </p>
              </div>
            ) : (
              <>
                <div className="rounded-md border">
                  <Table>
                    <TableHeader>
                      <TableRow>
                        <TableHead className="w-[50px]">
                          <Checkbox
                            checked={selectedUsers.size === users.length && users.length > 0}
                            onCheckedChange={toggleAllUsers}
                          />
                        </TableHead>
                        <TableHead>
                          <Button
                            variant="ghost"
                            onClick={() => handleSort('name')}
                            className="h-auto p-0 font-semibold hover:bg-transparent"
                          >
                            User
                            {sortField === 'name' && (
                              <span className="ml-1">
                                {sortDirection === 'asc' ? '↑' : '↓'}
                              </span>
                            )}
                          </Button>
                        </TableHead>
                        <TableHead>
                          <Button
                            variant="ghost"
                            onClick={() => handleSort('role')}
                            className="h-auto p-0 font-semibold hover:bg-transparent"
                          >
                            Role
                            {sortField === 'role' && (
                              <span className="ml-1">
                                {sortDirection === 'asc' ? '↑' : '↓'}
                              </span>
                            )}
                          </Button>
                        </TableHead>
                        <TableHead>
                          <Button
                            variant="ghost"
                            onClick={() => handleSort('status')}
                            className="h-auto p-0 font-semibold hover:bg-transparent"
                          >
                            Status
                            {sortField === 'status' && (
                              <span className="ml-1">
                                {sortDirection === 'asc' ? '↑' : '↓'}
                              </span>
                            )}
                          </Button>
                        </TableHead>
                        <TableHead>Assigned Editor</TableHead>
                        <TableHead>
                          <Button
                            variant="ghost"
                            onClick={() => handleSort('activity')}
                            className="h-auto p-0 font-semibold hover:bg-transparent"
                          >
                            Activity
                            {sortField === 'activity' && (
                              <span className="ml-1">
                                {sortDirection === 'asc' ? '↑' : '↓'}
                              </span>
                            )}
                          </Button>
                        </TableHead>
                        <TableHead className="text-right">Actions</TableHead>
                      </TableRow>
                    </TableHeader>
                    <TableBody>
                      {users.map((user, index) => {
                        const status = getUserStatus(user);

                        // Debug: Log user order in UI
                        console.log(`🎨 UI Render: User ${index + 1}: ${user.id} - ${user.transcription_count} transcriptions`);

                        return (
                          <TableRow key={user.id}>
                            <TableCell>
                              <Checkbox
                                checked={selectedUsers.has(user.id)}
                                onCheckedChange={() => toggleUserSelection(user.id)}
                              />
                            </TableCell>
                            <TableCell>
                              <div>
                                <div className="font-medium">{user.full_name || 'No name'}</div>
                                <div className="text-sm text-muted-foreground">{user.email}</div>
                              </div>
                            </TableCell>
                            <TableCell>
                              <Badge variant={
                                user.role === 'admin' ? 'default' :
                                user.role === 'editor' ? 'secondary' : 'outline'
                              }>
                                {user.role}
                              </Badge>
                            </TableCell>
                            <TableCell>
                              <Badge variant={status.variant}>
                                {status.label}
                              </Badge>
                            </TableCell>
                            <TableCell>
                              {user.assigned_editor_name || '-'}
                            </TableCell>
                            <TableCell>
                              <div className="text-sm">
                                <div className="flex items-center gap-1">
                                  <Activity className="h-3 w-3" />
                                  {user.transcription_count} transcriptions
                                </div>
                                <div className="flex items-center gap-1 text-muted-foreground">
                                  <Clock className="h-3 w-3" />
                                  {user.last_active 
                                    ? formatDistanceToNow(new Date(user.last_active), { addSuffix: true })
                                    : 'Never'
                                  }
                                </div>
                              </div>
                            </TableCell>
                            <TableCell className="text-right">
                              <DropdownMenu>
                                <DropdownMenuTrigger asChild>
                                  <Button variant="ghost" size="sm">
                                    <MoreHorizontal className="h-4 w-4" />
                                  </Button>
                                </DropdownMenuTrigger>
                                <DropdownMenuContent align="end">
                                  <DropdownMenuItem 
                                    onClick={() => {
                                      setNewRole(user.role);
                                      setRoleChangeDialog({ open: true, user });
                                    }}
                                  >
                                    Change Role
                                  </DropdownMenuItem>
                                  {user.role === 'transcriptionist' && (
                                    <DropdownMenuItem 
                                      onClick={() => {
                                        setSelectedEditorId(user.assigned_editor_id || '');
                                        setEditorAssignDialog({ open: true, user });
                                      }}
                                    >
                                      Assign Editor
                                    </DropdownMenuItem>
                                  )}
                                  <DropdownMenuSeparator />
                                  <DropdownMenuItem 
                                    className="text-destructive"
                                    onClick={() => {
                                      setConfirmDialog({
                                        open: true,
                                        title: 'Deactivate User',
                                        description: `Are you sure you want to deactivate ${user.email}? They will no longer be able to access the system.`,
                                        action: () => handleDeactivateUser(user.id)
                                      });
                                    }}
                                  >
                                    Deactivate User
                                  </DropdownMenuItem>
                                </DropdownMenuContent>
                              </DropdownMenu>
                            </TableCell>
                          </TableRow>
                        );
                      })}
                    </TableBody>
                  </Table>
                </div>

                {/* Pagination */}
                <div className="flex items-center justify-between px-2 py-4">
                  <div className="text-sm text-muted-foreground">
                    Page {currentPage} of {totalPages}
                  </div>
                  <div className="flex items-center space-x-2">
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={() => setCurrentPage(p => Math.max(1, p - 1))}
                      disabled={currentPage === 1}
                    >
                      <ChevronLeft className="h-4 w-4" />
                      Previous
                    </Button>
                    <Button
                      variant="outline"
                      size="sm"
                      onClick={() => setCurrentPage(p => Math.min(totalPages, p + 1))}
                      disabled={currentPage === totalPages}
                    >
                      Next
                      <ChevronRight className="h-4 w-4" />
                    </Button>
                  </div>
                </div>
              </>
            )}
          </CardContent>
        </Card>

        {/* Role Change Dialog */}
        <Dialog open={roleChangeDialog.open} onOpenChange={(open) => setRoleChangeDialog({ open, user: null })}>
          <DialogContent>
            <DialogHeader>
              <DialogTitle>Change User Role</DialogTitle>
              <DialogDescription>
                Update the role for {roleChangeDialog.user?.email}
              </DialogDescription>
            </DialogHeader>
            <div className="space-y-4 py-4">
              <div className="space-y-2">
                <Label>New Role</Label>
                <Select value={newRole} onValueChange={(value: UserRole) => setNewRole(value)}>
                  <SelectTrigger>
                    <SelectValue />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="admin">Admin</SelectItem>
                    <SelectItem value="editor">Editor</SelectItem>
                    <SelectItem value="transcriptionist">Transcriptionist</SelectItem>
                  </SelectContent>
                </Select>
              </div>
            </div>
            <DialogFooter>
              <Button variant="outline" onClick={() => setRoleChangeDialog({ open: false, user: null })}>
                Cancel
              </Button>
              <Button onClick={() => roleChangeDialog.user && handleRoleChange(roleChangeDialog.user.id, newRole)}>
                Update Role
              </Button>
            </DialogFooter>
          </DialogContent>
        </Dialog>

        {/* Editor Assignment Dialog */}
        <Dialog open={editorAssignDialog.open} onOpenChange={(open) => setEditorAssignDialog({ open, user: null })}>
          <DialogContent>
            <DialogHeader>
              <DialogTitle>Assign Editor</DialogTitle>
              <DialogDescription>
                Assign an editor to {editorAssignDialog.user?.email}
              </DialogDescription>
            </DialogHeader>
            <div className="space-y-4 py-4">
              <div className="space-y-2">
                <Label>Select Editor</Label>
                <Select value={selectedEditorId} onValueChange={(value: any) => setSelectedEditorId(value === 'none' ? '' : value)}>
                  <SelectTrigger>
                    <SelectValue placeholder="Choose an editor" />
                  </SelectTrigger>
                  <SelectContent>
                    <SelectItem value="none">No editor</SelectItem>
                    {editors.map((editor) => (
                      <SelectItem key={editor.id} value={editor.id}>
                        {editor.name} ({editor.email})
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>
            </div>
            <DialogFooter>
              <Button variant="outline" onClick={() => setEditorAssignDialog({ open: false, user: null })}>
                Cancel
              </Button>
              <Button 
                onClick={() => editorAssignDialog.user && handleEditorAssignment(
                  editorAssignDialog.user.id, 
                  selectedEditorId || null
                )}
              >
                Assign Editor
              </Button>
            </DialogFooter>
          </DialogContent>
        </Dialog>

        {/* Bulk Action Dialog */}
        <Dialog open={bulkActionDialog.open} onOpenChange={(open) => setBulkActionDialog({ open, action: '' })}>
          <DialogContent>
            <DialogHeader>
              <DialogTitle>
                {bulkActionDialog.action === 'role' && 'Bulk Role Change'}
                {bulkActionDialog.action === 'editor' && 'Bulk Editor Assignment'}
                {bulkActionDialog.action === 'deactivate' && 'Bulk Deactivation'}
              </DialogTitle>
              <DialogDescription>
                This action will affect {selectedUsers.size} selected user(s)
              </DialogDescription>
            </DialogHeader>
            <div className="space-y-4 py-4">
              {bulkActionDialog.action === 'role' && (
                <div className="space-y-2">
                  <Label>New Role</Label>
                  <Select value={newRole} onValueChange={(value: UserRole) => setNewRole(value)}>
                    <SelectTrigger>
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="admin">Admin</SelectItem>
                      <SelectItem value="editor">Editor</SelectItem>
                      <SelectItem value="transcriptionist">Transcriptionist</SelectItem>
                    </SelectContent>
                  </Select>
                </div>
              )}
              {bulkActionDialog.action === 'editor' && (
                <div className="space-y-2">
                  <Label>Select Editor</Label>
                  <Select value={selectedEditorId} onValueChange={(value: any) => setSelectedEditorId(value === 'none' ? '' : value)}>
                    <SelectTrigger>
                      <SelectValue placeholder="Choose an editor" />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="none">No editor</SelectItem>
                      {editors.map((editor) => (
                        <SelectItem key={editor.id} value={editor.id}>
                          {editor.name} ({editor.email})
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>
              )}
              {bulkActionDialog.action === 'deactivate' && (
                <div className="flex items-start gap-2 p-4 bg-destructive/10 rounded-lg">
                  <AlertCircle className="h-5 w-5 text-destructive mt-0.5" />
                  <div className="text-sm">
                    <p className="font-medium text-destructive">Warning</p>
                    <p className="text-muted-foreground">
                      Deactivating users will immediately revoke their access to the system. 
                      This action can be reversed by reactivating the users later.
                    </p>
                  </div>
                </div>
              )}
            </div>
            <DialogFooter>
              <Button variant="outline" onClick={() => setBulkActionDialog({ open: false, action: '' })}>
                Cancel
              </Button>
              <Button 
                variant={bulkActionDialog.action === 'deactivate' ? 'destructive' : 'default'}
                onClick={() => {
                  if (bulkActionDialog.action === 'role') {
                    handleBulkAction('role', { role: newRole });
                  } else if (bulkActionDialog.action === 'editor') {
                    handleBulkAction('editor', { editorId: selectedEditorId || null });
                  } else if (bulkActionDialog.action === 'deactivate') {
                    handleBulkAction('deactivate', { isActive: false });
                  }
                }}
              >
                {bulkActionDialog.action === 'deactivate' ? 'Deactivate Users' : 'Apply Changes'}
              </Button>
            </DialogFooter>
          </DialogContent>
        </Dialog>

            {/* Confirmation Dialog */}
            <Dialog open={confirmDialog.open} onOpenChange={(open) => !open && setConfirmDialog({ ...confirmDialog, open })}>
              <DialogContent>
                <DialogHeader>
                  <DialogTitle>{confirmDialog.title}</DialogTitle>
                  <DialogDescription>{confirmDialog.description}</DialogDescription>
                </DialogHeader>
                <DialogFooter>
                  <Button
                    variant="outline"
                    onClick={() => setConfirmDialog({ ...confirmDialog, open: false })}
                  >
                    Cancel
                  </Button>
                  <Button
                    variant="destructive"
                    onClick={() => {
                      confirmDialog.action();
                      setConfirmDialog({ ...confirmDialog, open: false });
                    }}
                  >
                    Confirm
                  </Button>
                </DialogFooter>
              </DialogContent>
            </Dialog>
          </TabsContent>

          <TabsContent value="uploads" className="space-y-6">
            {/* Upload Statistics Header */}
            <div className="flex flex-col gap-6">
              <div className="flex items-center justify-between">
                <div>
                  <h2 className="text-2xl font-semibold tracking-tight">Upload Statistics</h2>
                  <p className="text-muted-foreground">Monitor user upload activity and file usage</p>
                </div>
                <div className="flex items-center gap-2">
                  <Select value={timeRangeFilter} onValueChange={(value: any) => setTimeRangeFilter(value)}>
                    <SelectTrigger className="w-[120px]">
                      <SelectValue />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="7d">Last 7 days</SelectItem>
                      <SelectItem value="30d">Last 30 days</SelectItem>
                      <SelectItem value="90d">Last 90 days</SelectItem>
                      <SelectItem value="all">All time</SelectItem>
                    </SelectContent>
                  </Select>
                  <Button
                    onClick={fetchUploadStats}
                    variant="outline"
                    size="sm"
                    disabled={loadingUploadStats}
                  >
                    {loadingUploadStats ? (
                      <Loader2 className="h-4 w-4 animate-spin" />
                    ) : (
                      <RefreshCw className="h-4 w-4" />
                    )}
                    Refresh
                  </Button>
                </div>
              </div>

              {/* Overall Stats Cards */}
              {overallStats && (
                <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-5">
                  <Card>
                    <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                      <CardTitle className="text-sm font-medium">Total Files</CardTitle>
                      <Upload className="h-4 w-4 text-muted-foreground" />
                    </CardHeader>
                    <CardContent>
                      <div className="text-2xl font-bold">{overallStats.totalUploads.toLocaleString()}</div>
                      <p className="text-xs text-muted-foreground">
                        {overallStats.uploadsLast7Days} in last 7 days
                      </p>
                    </CardContent>
                  </Card>

                  <Card>
                    <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                      <CardTitle className="text-sm font-medium">Storage Used</CardTitle>
                      <HardDrive className="h-4 w-4 text-muted-foreground" />
                    </CardHeader>
                    <CardContent>
                      <div className="text-2xl font-bold">{overallStats.totalFileSizeGB} GB</div>
                      <p className="text-xs text-muted-foreground">
                        Avg {overallStats.averageFileSizeMB} MB per file
                      </p>
                    </CardContent>
                  </Card>

                  <Card>
                    <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                      <CardTitle className="text-sm font-medium">Active Users</CardTitle>
                      <Users className="h-4 w-4 text-muted-foreground" />
                    </CardHeader>
                    <CardContent>
                      <div className="text-2xl font-bold">{overallStats.totalUsers}</div>
                      <p className="text-xs text-muted-foreground">
                        Avg {overallStats.averageUploadsPerUser} uploads per user
                      </p>
                    </CardContent>
                  </Card>

                  <Card>
                    <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                      <CardTitle className="text-sm font-medium">Top Uploader</CardTitle>
                      <TrendingUp className="h-4 w-4 text-muted-foreground" />
                    </CardHeader>
                    <CardContent>
                      <div className="text-2xl font-bold">{overallStats.topUploader?.uploads || 0}</div>
                      <p className="text-xs text-muted-foreground truncate">
                        {overallStats.topUploader?.fullName || 'None'}
                      </p>
                    </CardContent>
                  </Card>

                  <Card>
                    <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
                      <CardTitle className="text-sm font-medium">Audio Minutes</CardTitle>
                      <Clock className="h-4 w-4 text-muted-foreground" />
                    </CardHeader>
                    <CardContent>
                      <div className="text-2xl font-bold">{overallStats.totalAudioMinutes?.toLocaleString() || 0}</div>
                      <p className="text-xs text-muted-foreground">
                        Avg {overallStats.averageAudioMinutesPerUser || 0} min per user
                      </p>
                    </CardContent>
                  </Card>
                </div>
              )}
            </div>

            {/* User Upload Statistics Table */}
            <Card>
              <CardHeader>
                <CardTitle>User Upload Activity</CardTitle>
                <CardDescription>
                  Upload statistics per user ({timeRangeFilter === 'all' ? 'all time' : `last ${timeRangeFilter}`})
                </CardDescription>
              </CardHeader>
              <CardContent>
                {loadingUploadStats ? (
                  <div className="flex items-center justify-center py-12">
                    <Loader2 className="h-8 w-8 animate-spin text-primary" />
                  </div>
                ) : uploadStats.length === 0 ? (
                  <div className="flex flex-col items-center justify-center py-12 text-center">
                    <BarChart3 className="h-12 w-12 text-muted-foreground mb-4" />
                    <h3 className="text-lg font-semibold">No upload data found</h3>
                    <p className="text-sm text-muted-foreground">
                      No completed uploads found for the selected time range
                    </p>
                  </div>
                ) : (
                  <div className="rounded-md border">
                    <Table>
                      <TableHeader>
                        <TableRow>
                          <TableHead>User</TableHead>
                          <TableHead>Role</TableHead>
                          <TableHead className="text-right">Total Files</TableHead>
                          <TableHead className="text-right">Audio Minutes</TableHead>
                          <TableHead className="text-right">Total Size</TableHead>
                          <TableHead className="text-right">Period Files</TableHead>
                          <TableHead className="text-right">Period Minutes</TableHead>
                          <TableHead>Last Upload</TableHead>
                        </TableRow>
                      </TableHeader>
                      <TableBody>
                        {uploadStats.map((user: any) => (
                          <TableRow key={user.userId}>
                            <TableCell>
                              <div>
                                <div className="font-medium">{user.fullName}</div>
                                <div className="text-sm text-muted-foreground">{user.email}</div>
                              </div>
                            </TableCell>
                            <TableCell>
                              <Badge variant={
                                user.role === 'admin' ? 'default' :
                                user.role === 'editor' ? 'secondary' : 'outline'
                              }>
                                {user.role}
                              </Badge>
                            </TableCell>
                            <TableCell className="text-right font-mono">
                              {user.totalUploads.toLocaleString()}
                            </TableCell>
                            <TableCell className="text-right font-mono">
                              {user.totalAudioMinutes?.toLocaleString() || 0}
                            </TableCell>
                            <TableCell className="text-right font-mono">
                              {user.totalFileSizeMB} MB
                            </TableCell>
                            <TableCell className="text-right font-mono">
                              {user.uploadsThisPeriod.toLocaleString()}
                            </TableCell>
                            <TableCell className="text-right font-mono">
                              {user.audioMinutesThisPeriod?.toLocaleString() || 0}
                            </TableCell>
                            <TableCell>
                              {user.lastUpload ? (
                                <div className="text-sm">
                                  <div className="flex items-center gap-1">
                                    <Calendar className="h-3 w-3" />
                                    {new Date(user.lastUpload).toLocaleDateString()}
                                  </div>
                                  <div className="text-muted-foreground">
                                    {formatDistanceToNow(new Date(user.lastUpload), { addSuffix: true })}
                                  </div>
                                </div>
                              ) : (
                                <span className="text-muted-foreground">Never</span>
                              )}
                            </TableCell>
                          </TableRow>
                        ))}
                      </TableBody>
                    </Table>
                  </div>
                )}
              </CardContent>
            </Card>
          </TabsContent>
        </Tabs>
      </div>
    </AdminRouteGuard>
  );
}
